body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: url(/static/media/bg.f794583d.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.categoryImg{
  width: 50% ;
  height: auto;
}

@media only screen and (max-width: 600px){
   .mobilAuswahl{
     display: -webkit-flex;
     display: flex;
     -webkit-flex-direction: column;
             flex-direction: column;
     margin-top: 25vw !important;
   }
}

.buttons{
  width: 100% !important;
}

.icons{
  font-size: 40px;
  color: darkgrey;

}

@media only screen and (max-width: 600px){
    .icons{
      margin: 2vw 0;
    }
}

.counterBasket{
  background: green;
  border-radius: 0.8em;
  color: #ffffff;
  display: inline-block;
  font-weight: bold;
  line-height: 1.6em;
  margin-right: 5px;
  text-align: center;
  width: 1.6em;
}

.hoverLink{
  border: 1px solid transparent;

}

.links{
  border: 1px solid grey;
  color: white !important;
}

.links:hover{
  border: 1px solid green;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.textDecoration{
  text-decoration: none !important;
}

@media only screen and (max-width: 600px){
  .links{
    margin: 1vw 0;
  }
}

@media only screen and (max-width: 600px){
  .mobile{
    margin: 2vw 0;
  }
}


.logo {
  font-family: 'Lobster', cursive;
  font-size: 20px;
  color: white;
}

.subImages{
  width: 20vw !important
}

.imgOrder{
  width: 20% !important;
  height: 20% !important;
}

.sizeP{
  font-size: 2vw !important;
}

.closeButton{
  font-size: 3vw !important;
  margin: 0 !important;
  position: absolute;
  top: -1vw;
  cursor: pointer;
}

.productImg{
  width: 50% !important;
  height: 50% !important;
}

@media only screen and (max-width: 600px){
  .mobileSingleproduct{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .productImg{
    width: 80% !important;
    margin: 0 auto;
    margin-bottom: 5vw;
  }
}


.dankeBox{
  display: none;
}

.infoBox{
  display: block;
}

.divBoxZero{
  display: none;
}

.showBox{
  display: block;
}

.showBoxDanke{
  display: -webkit-flex;
  display: flex;
}

.top{
  margin-top: 10vw;
}

@media only screen and (max-width: 600px){
  .mobileOrderImg{
    font-size: 5vw !important;
    font-weight: 500 !important;
  }

  .mobileOrderTitel{
    font-size: 6vw !important;
    margin-top: 5vw !important;
  }

  .mobileOrderSum{
    font-size: 5vw !important;
  }
}

